                // onHistoryCallback([], { noData: true });

                /** 标注相关 */
                //清计时器
                // if (_this_.Timer != null) {
                //     clearInterval(_this_.Timer);
                // }
                //我是实时更新标记 所以用了计时器
                // _this_.Timer = setInterval(() => {
                // var marks = _this_.getMarks();
                // let marks = [{
                //     id:'185145',
