                //     onDataCallback(marks);
                // }, 1000);
                // // 通过$once来监听定时器，在beforeDestroy钩子可以被清除。
                // this_vue.$once("hook:beforeDestroy", () => {
                //     clearInterval(this_vue.Timer);
                // });
                // let marks = [{
                //     id:'185145',
                //     // time:1675987200000 ,
                //     time:1675987200000 / 1000,
                //     color:'red',
                //     text:'三只松鼠股东减持2%。',
                //     label:'注',//注，
                //     labelFontColor:'#ffffff',
