            //订阅K线数据
            // Datafeed.Container.prototype.subscribeBars = function (symbolInfo, resolution, onRealtimeCallback, listenerGUID, onResetCacheNeededCallback) {
            Datafeed.Container.prototype.subscribeBars = function (symbolInfo, interval, onRealtimeCallback, subscribeUID, onResetCacheNeededCallback) {

                // console.log('onRealtimeCallback',onRealtimeCallback);
                // _this_.onRealtimeCallback = onRealtimeCallback;
                // _this_.bars.forEach(function (bar) {
                //     // console.log("bar",bar);
                //     onRealtimeCallback(bar)
                // });
                // this.on('pair_change', function () {
                //     onResetCacheNeededCallback();
                // });
                setInterval(() => {
                    subscribeKline({ symbol: symbolInfo.name, interval, uniqueID: subscribeUID, }, cb => onRealtimeCallback(cb))
                }, 5000)
            };
            //取消订阅K线数据
            Datafeed.Container.prototype.unsubscribeBars = function (listenerGUID) {
                this._barsPulseUpdater.unsubscribeDataListener(listenerGUID);
            };
            return new Datafeed.Container;
        },
        //标记
        getMarks() {
            var that = this;
            //自定义背景颜色
            var buyColor = { background: "#4caf50" };
            var sellColor = { background: "#e53935" };
            var marks = [];
            // bsAll是我的标记数据  根据你的情况而定
            // for (var i = 0; i < that.bsAll.length; i++) {
            //     var mark = {};
            //     mark.id = that.bsAll[i].id;  //id 是根据k线的id 确定的唯一性
            //     mark.time = that.bsAll[i].time / 1000; //时间也是唯一的  / 1000取决于后台返回你的数据
            //     mark.color = that.bsAll[i].bs === "买" ? buyColor : sellColor;
            //     mark.text = that.bsAll[i].bs;
            //     mark.label = that.bsAll[i].bs; 
            //     mark.labelFontColor = "#fff";//文字颜色
            //     mark.minSize = 20;//尺寸
            //     marks.push(mark);
            // }
            //标记数据
            let bsAll = [{
                id: 232323,
                time: '2023-02-10 17:00:00',
                type: 'buy',
                text: '港股节期停市！',
                label: '注',
            }, {
                id: 23233,
                time: '2023-02-05 17:00:00',
                type: 'sell',
                text: '三只松鼠股东减持2%！',
                label: '事',
            }]
            bsAll = []
            bsAll.map((v, o) => {
                var mark = {};
                mark.id = v.id;  //id 是根据k线的id 确定的唯一性
                // mark.time = v.time / 1000; //时间也是唯一的  / 1000取决于后台返回你的数据
                mark.time = new Date(v.time).getTime() / 1000;
                mark.color = v.type == "buy" ? buyColor : sellColor;
                mark.text = v.text;
                mark.label = v.label;
                mark.labelFontColor = "#fff";//文字颜色
                mark.minSize = 20;//尺寸
                marks.push(mark);
            });
            return marks;
        },
        //初始化图表
        async init(callback) {
            this.feed = this.createFeed();
            this.tradingViewConfig.symbol = this.currency1 + ":" + this.currency2;//'AAPL'
            console.log(this.tradingViewConfig, 'this.tradingViewConfig')
            this.tradingViewConfig.datafeed = this.feed;
            // this.tradingViewConfig.datafeed = new Datafeeds.UDFCompatibleDatafeed("https://demo-feed-data.tradingview.com");//this.feed;
            this.chart = window.tvWidget = new TradingView.widget(this.tradingViewConfig);
            this.chart.onChartReady(() => {
                this.updateKLine();
                this.isShow = true;
                //切换分时图
                // this.chart.subscribe("time_interval",res=>{
                //     localStorage.setItem('tradingview_interval',res.label);
                //     this.bars=[];
                //     this.changePair();
                //     sendMsg({cmd:'tradingview',data:res.label});
                // });
                //重置按钮 undo_redo_state_changed：缩放改变事件
                this.chart.subscribe("reset_scales", () => {
                    this.updateKLine();
                })
                const onLoad = ({ from, to }) => {
                    // console.log(from, to, '55')
                    // console.log('onVisibleRangeChanged:',from, to,getThisTime(from).now)
                    // console.log('onVisibleRangeChanged:',from, to,this.bars[0].time/1000,this.page.finish,this.page.loading);
                    let t = from - this.bars[0].time / 1000;
                    // if(this.bars.length>0 && from==this.bars[0].time/1000){
                    if (this.isShowCharts && !this.page.loading && !this.page.finish && this.bars.length > 0 && t < 24 * 60 * 60 && t > 0) {//tradingview转成了整点时间，需要对比小于24小时以内。
                        console.log('第一条k线：', from);
                        this.page.current += 1;
                        this.getList(() => {
                            console.log('更新图表')
                            this.changePair();
                        });
                    }
                }
                //订阅可见逻辑范围更改
                //判断是否需要获取历史数据
                this.chart.activeChart().onVisibleRangeChanged().subscribe(null, ({ from, to }) => {
                    // throttle(onLoad({ from, to }), 50)

                    // // console.log('onVisibleRangeChanged:',from, to,getThisTime(from).now)
                    // // console.log('onVisibleRangeChanged:',from, to,this.bars[0].time/1000,this.page.finish,this.page.loading);
                    // let t=from-this.bars[0].time/1000;
                    // // if(this.bars.length>0 && from==this.bars[0].time/1000){
                    // if(!this.page.loading && !this.page.finish && this.bars.length>0 &&  t< 24*60*60 && t>0){//tradingview转成了整点时间，需要对比小于24小时以内。
                    //     console.log('第一条k线：',from);
                    //     this.page.current+=1;
                    //     // this.getList(()=>{
                    //     //     console.log('更新图表')
                    //     //     this.changePair();
                    //     // });
                    // }
                });
                //函数节流方法
                function throttle(fn, dalay, atleast = 0) {
                    let timer = null
                    let previous = null
                    return (...args) => {
                        let now = +new Date() //获取当前时间戳
                        !previous ? now : previous
                        if (atleast && now - previous > atleast) {
                            fn.apply(this, args)
                            // 重置上一次开始时间为本次结束时间
                            previous = now
                            clearTimeout(timer)
                        } else {
                            clearTimeout(timer)
                            timer = setTimeout(() => {
                                fn.apply(this, args)
                                previous = null
                            }, dalay)
                        }
                    }
                }
                //均线
                this.chart.chart().createStudy("Moving Average", false, false, [5, "close", 0], null, { "Plot.color": "#e7eff2", "Plot.linewidth": 0, });
                this.chart.chart().createStudy("Moving Average", false, false, [10, "close", 0], null, { "Plot.color": "#e6f562", "Plot.linewidth": 0, });
                this.chart.chart().createStudy("Moving Average", false, false, [20, "close", 0], null, { "Plot.color": "#cc01bf", "Plot.linewidth": 0, });


                this.chart.activeChart().resetData();
                callback && callback();
            });
        },
        //创建随机数据
        createData() {

            //随机k线图
            setInterval(() => {
                // setTimeout(() => {

                let data = this.bars;
                //time：日期(毫秒单位时间戳)，close：收盘价，open：开盘价，high：最高价，low：最低价，volume：交易量
                let lastPrice = data[data.length - 1].low;
                let p = lastPrice * 0.1;
                data.push({
                    time: new Date().getTime(),
                    close: createAutoData(1, lastPrice + p, lastPrice - p)[0],
                    open: createAutoData(1, lastPrice + (p / 2), lastPrice - (p / 2))[0],
                    high: createAutoData(1, lastPrice + p, lastPrice - p)[0],
                    low: createAutoData(1, lastPrice + p, lastPrice - p)[0],
