                low: createAutoData(1, lastPrice + p, lastPrice - p)[0],
                volume: 100000 * Math.random()
            };
            this.bars.push(obj);
            // return obj;
            callback && callback(obj)
            // this.changePair();
        },
        //根据tushare获取
        getList2() {
            //插入字符
