<template>
<div class="h100p flexColumn" style="border-left:1px solid #434651;min-width: 300px;overflow: auto;">
    <!-- <div class="borderBottom p10">
        <div class="f18 fb">
            <span class="op7 mr10">000792</span>
            <span class="orange ">{{ area.text }}</span>
        </div>
        <div class="red ">
            <span class="f20 fb">26.52</span>
            <span class="ml10">0.20</span>
            <span class="ml10">+0.76%</span>
        </div>
    </div> -->
    <div class="borderBottom p10 flexBetween">
        <div class="alignCenter">
            <div class="f18 fb">
                <span :class="[color]">{{ info.close | format(2) }}</span>
                <span class=" ml10 circleBox">{{ area.text }}</span>
            </div>
            <div class="ml10" :class="[color]">
                <div class="f14 fb">{{ info.close-info.open | format(2,'',1) }}</div>
                <!-- <span class="ml10">0.20</span> -->
                <div class=" f12">
                    <Icon v-if="info.close-info.open>0" type="md-arrow-dropup" />
                        <Icon v-else-if="info.close-info.open<0" type="md-arrow-dropdown"/>
                        {{ (info.close-info.open)/info.open*100 | toPercent(2) }}
                </div>
            </div>
        </div>
        <div>
            <img :src="require('@/assets/img/collect.svg')" :title="'收藏'" @click="addFollow()" class="pointer" style="width:20px;height:20px;" />
            <!-- <img :src="require('@/assets/img/collectFill.svg')" :title="'取消收藏'" @click="cancelFollow()" class="pointer" style="width:20px;height:20px;" /> -->
            <img :src="require('@/assets/img/warn.svg')" :title="'预警'" @click.stop="addWarn()" class="active_t ml5" style="width:20px;height:20px;" />
            <!-- <img :src="require('@/assets/img/warn_red.svg')" :title="'预警'" class="active_t ml5" style="width:20px;height:20px;" /> -->
        </div>
    </div>
    <div class="handicapTitle">买卖五档</div>
    <div class="borderBottom ">
        <div class="flexBox">
            <div class="borderRight alignCenter p5 op7" style="width: 2em;">卖盘</div>
            <div class="center" style="flex:1;">
                <div v-for="(v,o) in 5" :key="o" class="gradeList">
                    <div class="op7">{{6-(o+1)}}</div>
                    <div :class="[color]">{{ sellPrices[o] | format(2) }}</div>
                    <div>{{ sellAmounts[o] | format(2) }}</div>
                </div>
            </div>
        </div>
        <div v-if="sellAmountsSum==0&&buyAmountsSum==0" class="buySellRatio">
            <div class="buyRatio" :style="{width:'50%'}"></div>
            <div class="sellRatio" :style="{width:'50%'}"></div>
        </div>
        <div v-else class="buySellRatio"><!--占比：卖出总数量/(买入总数量+卖出总数量)-->
            <div class="buyRatio" :style="{width:(sellAmountsSum/(sellAmountsSum+buyAmountsSum)*100)+'%'}"></div>
            <div class="sellRatio" :style="{width:(buyAmountsSum/(sellAmountsSum+buyAmountsSum)*100)+'%'}"></div>
        </div>
        <div class="flexBox">
            <div class="borderRight alignCenter p5 op7" style="width: 2em;">买盘</div>
            <div class="center" style="flex:1;">
                <div v-for="(v,o) in 5" :key="o" class="gradeList">
                    <div class="op7">{{o+1}}</div>
                    <div :class="[color]">{{ buyPrices[o] | format(2) }}</div>
                    <div>{{ buyAmounts[o] | format(2) }}</div>
                </div>
            </div>
        </div>
    </div>
    <!-- 关键数据 -->
    <div class="handicapTitle">盘口</div>
    <div>
        <div class="grid_2">
            <div class="flexBetween">
                <div class="op7 ">最高</div>
                <div :class="[color]">{{ info.highest | format(2) }}</div>
            </div>
            <div class="flexBetween">
                <div class="op7 ">开盘</div>
                <div :class="[color]">{{ info.lowest | format(2) }}</div>
            </div>
        </div>
        <div class="grid_2">
            <div class="flexBetween">
                <div class="op7 ">最低</div>
                <div :class="[color]">{{ info.open | format(2) }}</div>
            </div>
            <div class="flexBetween">
                <div class="op7 ">昨收</div>
                <div :class="[color]">{{ info.close | format(2) }}</div>
            </div>
        </div>
        <div class="grid_2">
            <div class="flexBetween">
                <div class="op7 ">成交量</div>
                <div class="">{{ info.currentAmount | format(2) }}</div>
            </div>
            <div class="flexBetween">
                <div class="op7 ">成交额</div>
                <div class="">{{ '' | format(2) }}</div>
            </div>
        </div>
        <div class="grid_2">
            <div class="flexBetween">
                <div class="op7 ">量比</div>
                <div class="">{{ info.quantityrelative | format(2) }}</div>
            </div>
            <div class="flexBetween">
                <div class="op7 ">委比</div>
                <div class="">{{ info.committeeThan | format(2) }}</div>
            </div>
        </div>
        <div class="grid_2">
            <div class="flexBetween">
                <div class="op7 ">委差</div>
                <div class="">{{ info.committeeSent | format(2) }}</div>
            </div>
            <div></div>
        </div>
    </div>
    <div class="handicapTitle">分时成交</div>
    <div class=""  @scroll="scrollEvent" style="flex:1;overflow: auto;">
        <div v-for="(v,o) in list" :key="o" class="grid_4">
            <!-- <div class="cloc">{{ v.time?$dayjs('2023-03-31 16:41:00').format('HH:mm'):'00:00' }}</div> -->
            <div class="cloc">{{ v.time?$dayjs(v.time).format('HH:mm'):'00:00' }}</div>
            <div class="center" :class="v.direction=='B'?'red':'green'">{{ v.price }}</div>
            <div class="center" :class="v.direction=='B'?'red':'green'">{{ v.residueAmount }}</div>
            <div class="tright">{{ v.amount }}</div>
        </div>
        <div v-if="list.length==0" class="center cloc f12 mt10">暂无数据</div>
    </div>
    <warnPop v-model="isWarnPop" :area="area"></warnPop>
</div>
</template>

<script>
import warnPop from '@/components/warnPop.vue'
export default {
    name: '',
    props: {
        area:{
            type:Object,
            default:()=>{}
        }
    },
    components: {
        warnPop
    },
    data () {
        return {
            info:{},
            isWarnPop:false,
            list:[],
            page:{
                "current": 1,
                "size": 20,
                // "size": 5,
                loading:false,
                finish:false,
            },
            
        }
    },
    mounted() {
        
        // this.getInfo();
        // this.getList();
        this.init();
        //需要调用的接口 trading_view_close,trading_view_com_page,snapshot_last
    },
    methods: {
        //取消关注
        async cancelFollow(){
            let d={
                area: this.area.type,
            };
            this.$api.price_alert_cancel(d).then(res=>{
                this.$Message.success('关注成功!');
            });
        },
        //添加关注
        async addFollow(){
            let d={
                area: this.area.type,
            };
            this.$api.price_alert_add(d).then(res=>{
                this.$Message.success('关注成功!');
            });
        },
        //重新初始化分页
        init(){
            this.page={
                "current": 1,
                "size": 20,
                loading:false,
                finish:false,
            }
            this.list=[];
            this.getList();
        },
        search(){
            this.page.current=1;
            this.getList();
        },
        // 滚定监听
        scrollEvent(e) {
            let that=this;
            if (Math.ceil(e.currentTarget.scrollTop + e.currentTarget.clientHeight) >=e.currentTarget.scrollHeight-20) {   //容差：20px
                console.log('滚动到底部');
                if(!that.page.finish && !that.page.loading){
                    that.page.current+=1;
                    that.getList();
                }
            }
        },
        //列表
        async getList(){
            if(this.page.loading){
                return;
            }
            let d={
                area: this.area.type,
                current: this.page.current,
                size: this.page.size,
            };
            this.page.loading=true;
            let res = await this.$api.trading_minute_page(d);
            let list = res.list || [];
            if (list.length >= d.size) {
                this.page.finish = false;
            } else {
                this.page.finish = true;
            }
            if(d.current==1){
                this.list=list;
            }else{
                this.list=this.list.concat(list);
            }
            this.page.loading=false;

            console.log('分时成交：',list)
        },
        //添加预警
        addWarn(){
            this.isWarnPop=true;
        },
        //盘口
        async getInfo(){
            let d={
                area: this.area.type,
            };
            this.info={};
            let res = await this.$api.snapshot_last(d);
            this.info = res||{};
        },
    },
    computed:{
        //买入价格
        buyPrices(){
            if(this.info.buyPrices){
                return JSON.parse(this.info.buyPrices)
            }
            return ['','','','',''];
        },
        //买入数量
        buyAmounts(){
            if(this.info.buyAmounts){
                return JSON.parse(this.info.buyAmounts)
            }
            return ['','','','',''];
        },
        //买入总数量
        buyAmountsSum(){
            let sum=0;
            if(this.info.buyAmounts){
                JSON.parse(this.info.buyAmounts).map(v=>{
                    sum+=v;
                });
            }
            return sum;
        },
        //卖出总数量
        sellAmountsSum(){
            let sum=0;
            if(this.info.sellAmounts){
                JSON.parse(this.info.sellAmounts).map(v=>{
                    sum+=v;
                });
            }
            return sum;
        },
        //卖出价格
        sellPrices(){
            if(this.info.sellPrices){
                return JSON.parse(this.info.sellPrices).reverse()
            }
            return ['','','','',''];
        },
        //卖出数量
        sellAmounts(){
            if(this.info.sellAmounts){
                return JSON.parse(this.info.sellAmounts).reverse()
            }
            return ['','','','',''];
        },
        //展示颜色。红：由当前价（或收盘价）-开盘价>0，反之绿，平价灰。
        color(){
            if(!this.info.open || !this.info.close){
                return ''
            }
            if(this.info.close-this.info.open>0){
                return 'red'
            }
            if(this.info.close-this.info.open<0){
                return 'green'
            }
            return '';
        }
    },
    watch:{
        'area':{
            handler:function(val,value){
                this.getInfo();
                this.init();
            },
            deep: true,
            immediate: false
        }
    }
}
</script>

<style scoped lang='less'>
.gradeList{
    display: flex;
}
.gradeList>div{
    flex:2;
    padding:2px 0;
}
.gradeList>div:first-child{
    flex:1;
}
.buySellRatio{
    height:4px;
    display: flex;
}
.buyRatio{
    
    background:#e53935;
}
.sellRatio{
    
    background:#4caf50;
}
.handicapTitle{
    font-size: 12px;
    padding:2px 5px;
    color:#ccc;
    background: #333;
}
.circleBox{
    padding: 4px 5px;
    border-radius: 8px;
    background: #303044;
    font-size: 14px;
    font-weight: normal;
    color: #c3c2d4;
}
</style>