<template>
<div class="borderTop">
    <div class="flexBetween">
        <div class="trrDownTab">
            <div :class="{'active':active==0}" @click="active=0">
                {{$t("新闻舆论")}}
            </div>
            <div :class="{'active':active==1}" @click="active=1">
                {{$t("观点分析")}}
                <!-- <span class="tagB ml5">12</span> -->
            </div>
            <div :class="{'active':active==2}" @click="active=2">
                {{$t("讨论区")}}
                <!-- <span class="tagB ml5">12</span> -->
            </div>
            
        </div>
        <!--展开-->
        <div class="PMLmainTopRight" v-if="!isOpen" @click="toggle('fold')">
            <span class="mr5">{{$t("fold")}}</span>
            <img :src="require('@/assets/img/arrow.svg')" style="width:12px;height:12px;transform: rotate(-90deg);"/>
        </div>
        <div class="PMLmainTopRight" v-if="isOpen" @click="toggle('open')">
            <span  class="mr5">{{$t("open")}}</span>
            <img :src="require('@/assets/img/arrow.svg')" style="width:12px;height:12px;transform: rotate(90deg);"/>
        </div>
    </div>
    <div style="flex:1;overflow-y:auto;">
        <div v-if="active==0" class="flexBox h100">
            <div style="overflow-y:auto;width:50%;">
                <news :area="area"></news>
            </div>
            <div style="overflow-y:auto;width:50%;">
                <news2 :area="area"></news2>
            </div>
        </div>
        <analyse v-if="active==1" :area="area"></analyse>
        <!-- <discuss v-show="active==2" :area="area"></discuss> -->
        <discuss v-if="active==2" :area="area"></discuss><!--非会员看不了这个页面-->
    </div>
</div>
</template>

<script>
import news from './news.vue'
import news2 from './news2.vue'
import analyse from './analyse.vue'
import discuss from './discuss.vue'
export default {
    name: '',
    props: {
        area:{
            type:Object,
            default:()=>{}
        },
        isOpen:{
            type:Boolean,
            default:false,
        }
    },
    components: {
        news,news2,analyse,discuss
    },
    data () {
        return {
            active:0,
            
        }
    },
    mounted() {

    },
    methods: {
        //切换展开折叠
        toggle(val){
            this.$emit('toggle',val);
        },
    },
}
</script>

<style scoped lang='less'>
.borderTop{
    display: flex;
    flex-direction: column;
}
.trrDownTab{
    display: flex;border-bottom:1px solid #333;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-right: 1px solid #333;
    
    >div{
        cursor: pointer;.flexCenter;height:40px;min-width:100px;padding:0 20px;border-right:1px solid #333;
        &.active{background: #000;color:#fff;cursor: default;}
        &:hover{background: #000;}
    }
}
.tagB{
        display: inline-block;
    background: rgba(56, 97, 251, 0.1);
    border-radius: 8px;
    padding: 4px 14px;
    margin: 0 10px 0 10px;
    font-size: 12px;
}
.list{
    cursor: pointer;
    padding:8px 12px;
}
.list:hover{
    color:#fff;
    
    background: #000;
    .title{opacity: 10;color:@yellow;}
    // .op5{opacity: 10;}
}
.PMLmainTopRight {
    .alignCenter;
    cursor: pointer;
    padding: 5px 10px;
    margin-right:10px;
    border-radius: 15px;
    background:#333;
    &:active {
    background: #2d2d2d;
    }
    &:hover{opacity:0.8;}
}
</style>