        <div class="fb red" :class="{'red':v.pct_chg>0,'green':v.pct_chg<0}">{{v.pct_chg>0?'+'+v.price:'-'+v.price}}</div>
        <div class="red" :class="{'red':v.pct_chg>0,'green':v.pct_chg<0}">{{v.pct_chg>0?'+'+v.pct_chg:v.pct_chg}}%</div>
    </div>
    <div class="center mt10 cloc">暂无数据</div>
