<div class=" borderRight flexColumn h100p" style="overflow: auto;">
    <div class="menus  alignCenter">
                <div class="mr5 cloa f12">市场</div>
                <div v-for="(v,o) in areaList" :key="o" class="menu" :class="{'active':active==o}" @click="active=o;search();">{{v.text}}</div>
                <!-- <div class="menu" :class="{'active':active==0}" @click="active=0">全局概览</div>
                <div class="menu" :class="{'active':active==1}" @click="active=1;$router.push('/exchange_quotation')">交易所行情</div>
                <div class="menu" :class="{'active':active==2}" @click="active=2;;$router.push('/ccer_quotation')">全国CCER行情</div>
                <div class="menu" :class="{'active':active==3}" @click="active=3;$router.push('/manager_quotation')">经纪商行情</div>
                <div class="menu" :class="{'active':active==4}" @click="active=4;$router.push('/external_quotation')">国外碳价行情</div> -->
                <!-- <Button shape="circle" class="ml10 right" style="min-width:80px;" @click="isFilterPop=true">条件选品种</Button> -->
            </div>
    <div class="borderTop typeListBox" style="flex:1;overflow: auto;">
        <div v-for="(v,o) in list" :key="o" @click="select(v,o)" class="typeList flexBetween" :class="{'active':v.areaName==area.text}">
            <div>
                <div class="">{{v.areaName}}</div>
                <!-- <div class="op7">{{v.price}}</div> -->
            </div>
            <div class="tright cloc ml10">
                <div class="fb" :class="[$base.getColor(v.close,v.open)]">{{v.price}}</div>
                <div class="" :class="[$base.getColor(v.close,v.open)]">{{ v.increase*100 | toPercent(2,1) }}</div>
            </div>
            <!-- <div class="fb red" :class="{'red':v.pct_chg>0,'green':v.pct_chg<0}">{{v.pct_chg>0?'+'+v.price:'-'+v.price}}</div>
            <div class="red" :class="{'red':v.pct_chg>0,'green':v.pct_chg<0}">{{v.pct_chg>0?'+'+v.pct_chg:v.pct_chg}}%</div> -->
        </div>
        <div v-if="list.length==0" class="center mt10 cloc">暂无数据</div>
    </div>
</div>
</template>

<script>
function rand(min, max) {
    return parseInt(Math.random() * (max - min + 1) + min);
}
export default {
    name: '',
    props: {
        // list:{
        //     type:Array,
        //     default:()=>[]
        // },
        area:{
            type:Object,
            default:()=>{}
        }
    },
    components: {

    },
    data () {
        return {
            // list:[],
            // areaList:this.$store.state.app.areaList,//区域列表
            // area:{},
            active:0,
            // areaList:this.$store.state.app.areaList,//区域列表
            areaList:[{
                type:'china',
                text:'国内',
            },{
                type:'eu',
                text:'国外',
            },{
                type:'other',
                text:'其他',
            }],//区域列表
            listActive:-1,
            list:[],
            page:{
                "current": 1,
                "size": 20,
                // "size": 5,
                loading:false,
                finish:false,
            },
        }
    },
    mounted() {
        this.getList();
    },
    methods: {
        trigger(){
            let v=this.list[0];
            this.listActive=v;
            this.$emit('select',{
                type:v.area,
                text:v.areaName,
            });
        },
        select(v,o){
            this.listActive=o;
            this.$emit('select',{
                type:v.area,
                text:v.areaName,
            });
        },
        search(){
            this.page.current=1;
            this.getList();
        },
        // 滚定监听
        scrollEvent(e) {
            let that=this;
            if (Math.ceil(e.currentTarget.scrollTop + e.currentTarget.clientHeight) >=e.currentTarget.scrollHeight-20) {   //容差：20px
                console.log('滚动到底部');
                if(!that.page.finish && !that.page.loading){
                    that.page.current+=1;
                    that.getList();
                }
            }
        },
        //列表
        async getList(){
            if(this.page.loading){
                return;
            }
            // china：中国
            // other：国外
            // eu：其他
            let d={
                area: this.areaList[this.active].type,
                current: this.page.current,
                size: this.page.size,
            };
            this.page.loading=true;
            let res = await this.$api.market_page(d);
            let list = res.list || [];
            if (list.length >= d.size) {
                this.page.finish = false;
            } else {
                this.page.finish = true;
            }
            if(d.current==1){
                this.list=list;
            }else{
                this.list=this.list.concat(list);
            }
            this.page.loading=false;
        },
    },
}
</script>

