<template>
    <div class="borderTop wh100" style="overflow: auto;">
        <div  class=" wh100" @scroll="scrollEvent" style="overflow: auto;">
            <div v-for="(v, o) in list" :key="o" @click="toDetail(v)" class="list alignCenter">
                <span class="alignCenter" style="flex:1;">
                    <!-- <span class=" op7">[&nbsp;<span class="">{{v.source}}</span>&nbsp;]</span> -->
                    <span class=" op7">[&nbsp;<span class="">资讯</span>&nbsp;]</span>
                    <!-- <span class=" op7"><span class="">{{v.source}}</span></span> -->
                    <span class="ml10  title text_deal" :title="v.title" style="flex:1;">{{v.title}}</span>
                </span>
                <span class="op5 f12">{{v.resourceTime | getDateDiff}}</span>
            </div>
            <noData v-if="!page.loading" :list="list" style="flex-shrink: 0; width: 100%;"></noData>
            <!-- <noData :list="list" style="flex-shrink: 0; width: 100%;"></noData> -->
        </div>
    </div>
</template>
    
<script>
export default {
    name: '',
    props: {
        area:{
            type:Object,
            default:()=>{}
        }
    },
    components: {

    },
    data() {
        return {
            active: 0,
            list:[],
            page:{
                "type": '热点新闻',
                // area:'china',
                "current": 1,
                "size": 20,
                loading:false,
                finish:false,
            },
        }
    },
    mounted() {
        this.getList();
    },
    methods: {
        toDetail(v){
            // localStorage.setItem('information_detail',JSON.stringify(v));
            // this.$router.push(`/informationDetail`);
            // window.open('/informationDetail','_blank');
            window.open(`/informationDetail?id=${v.keyId}`,'_blank');
        },
        // 滚定监听
        scrollEvent(e) {
            let that=this;
            if (Math.ceil(e.currentTarget.scrollTop + e.currentTarget.clientHeight) >=e.currentTarget.scrollHeight-20) {   //容差：20px
                console.log('滚动到底部');
                if(!that.page.finish && !that.page.loading){
                    that.page.current+=1;
                    that.getList();
                }
            }
        },
        //根据接口获取
        async getList(){
            if(this.page.loading){
                return;
            }
            let d={
                // type: this.page.type=='全部'?'':this.page.type,
                type:'热点新闻',
                area: this.page.area,
                current: this.page.current,
                size: this.page.size,
            };
            this.page.loading=true;
            let res = await this.$api.news_page(d);
            let list = res.list || [];
            if (list.length >= d.size) {
                this.page.finish = false;
            } else {
                this.page.finish = true;
            }
            if(d.current==1){
                this.list=list;
            }else{
                this.list=this.list.concat(list);
            }
            this.page.loading=false;
        },
    },
}
</script>
    
<style scoped lang='less'>
.list {
    cursor: pointer;
    padding: 8px 12px;
}

.list:hover {
    color: #fff;

    background: #000;

    .title {
        opacity: 10;
        color: @yellow;
    }

    // .op5{opacity: 10;}
}</style>