<template>
    <div @scroll="scrollEvent" style="flex:1;overflow:auto;">
        <!-- <div class="alignCenter">
            <div>#</div>
            <div>成交价</div>
            <div>成交量</div>
            <div style="flex:1;">比例</div>
        </div> -->
        <table cellspacing="0" style="width:100%;">
            <thead>
                <tr class="cloc center">
                    <td width="5%">#</td>
                    <td width="10%">成交价</td>
                    <td width="10%">成交量</td>
                    <td width="75%">比例(满格{{sumAmount}}手)</td>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(v,o) in list" :key="o" class="center cloc">
                    <td>{{ o+1 }}</td>
                    <td class="red">{{ v.price | toFixed(2) }}</td>
                    <td>{{ v.amount }}</td>
                    <td class="tleft" style="padding:0;">
                        <div class="relative">
                            <div class="bgBar red" :style="{width:(v.amount/sumAmount*100)+'%'}"></div>
                            <div class="relative" style="z-index: 2;">{{ (v.amount/sumAmount*100) | toPercent(1) }}</div>
                        </div>
                    </td>
                </tr>
                <tr v-if="list.length==0">
                    <td class="center cloc" colspan="4">暂无数据</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
export default {
    name: 'WebTimeTable',
    props: {
        area:{
            type:Object,
            default:()=>{}
        }
    },
    data() {
        return {
            list:[],
            page:{
                "current": 1,
                "size": 20,
                loading:false,
                finish:false,
            },
            sumAmount:1000,//总数量
        };
    },
    mounted() {
        // this.getList();
    },
    methods: {
        //重新初始化分页
        init(){
            this.page={
                "current": 1,
                "size": 20,
                loading:false,
                finish:false,
            }
            this.list=[];
            this.getList();
        },
        // 滚定监听
        scrollEvent(e) {
            let that=this;
            if (Math.ceil(e.currentTarget.scrollTop + e.currentTarget.clientHeight) >=e.currentTarget.scrollHeight-20) {   //容差：20px
                console.log('滚动到底部');
                if(!that.page.finish && !that.page.loading){
                    that.page.current+=1;
                    that.getList();
                }
            }
        },
        //列表
        async getList(){
            if(this.page.loading){
                return;
            }
            let d={
                area: this.area.type,
                // area: 'HU_BEI',
                current: this.page.current,
                size: this.page.size,
            };
            this.page.loading=true;
            let res = await this.$api.getTradeList(d);
            let list = res.list || [];
            console.log('分价：',list)
            if (list.length >= d.size) {
                this.page.finish = false;
            } else {
                this.page.finish = true;
            }
            if(d.current==1){
                this.list=list;
            }else{
                this.list=this.list.concat(list);
            }
            this.page.loading=false;
        },
    },
    watch:{
        'area':{
            handler:function(val,value){
                this.init();
            },
            deep: true,
            immediate: true
        }
    }
};
</script>

<style lang="less" scoped>
table td{
    // border:1px solid #434651;
    border:1px solid #202227;
}
tbody tr{
    text-align: right;
}
tbody tr:hover{
    background: rgb(15, 15, 15);
}
thead tr td{
    padding:2px 5px;
}
tbody tr td{
    padding:0px 5px;
}
.bgBar{
    position: absolute;
    // width: 100%;
    height: 100%;
    top:0;
    left:0;
    z-index: 1;
}
.bgBar.red{
    background: #e53935;
}
.bgBar.green{
    background: #4caf50;
}
</style>