<template>
    <div class="" style="flex:1;overflow:auto;">
        <timeView_tradingview :list="list" :area="area" ref="timeView1" class="relative wh100"></timeView_tradingview>
    </div>
</template>

<script>
import timeView_tradingview from './timeView_tradingview'
export default {
    name: 'WebSuperReplay',
    components: {
        timeView_tradingview
    },
    props:{
        area:{
            type:Object,
            default:()=>{}
        }
    },
    data() {
        return {
            areaActive:-1,
            list:[],
            page:{
                "current": 1,
                "size": 200,
                // "size": 5,
                loading:false,
                finish:false,
            },
        };
    },
    mounted() {
        this.getList();
    },
    methods: {
        //重新初始化分页
        init(){
            this.page={
                "current": 1,
                "size": 20,
                loading:false,
                finish:false,
            }
            this.list=[];
            this.getList();
        },
        showDom(){
            this.$nextTick(()=>{
                // this.$refs['timeView'].initData();
                this.$refs['timeView1'].getData();
            });
        },
        // 滚定监听
        scrollEvent(e) {
            let that=this;
            if (Math.ceil(e.currentTarget.scrollTop + e.currentTarget.clientHeight) >=e.currentTarget.scrollHeight-20) {   //容差：20px
                console.log('滚动到底部');
                if(!that.page.finish && !that.page.loading){
                    that.page.current+=1;
                    that.getList();
                }
            }
        },
        //列表
        async getList(){
            if(this.page.loading){
                return;
            }
            let d={
                area: this.area.type,//'hubei',
                current: this.page.current,
                size: this.page.size,
            };
            this.page.loading=true;
            let res = await this.$api.trading_page(d);
            let list = res.list || [];
            if (list.length >= d.size) {
                this.page.finish = false;
            } else {
                this.page.finish = true;
            }
            if(d.current==1){
                this.list=list;
            }else{
                this.list=this.list.concat(list);
            }
            this.page.loading=false;

            this.showDom();
        },
    },
    watch:{
        'area':{
            handler:function(val,value){
                // console.log('修改')
                this.init();
            },
            deep: true,
            immediate: false
        }
    }
};
</script>

