    },
    components: {

    },
    data() {
        return {
            isShow: true,//显示k线图
            tradingViewConfig: TradingViewConfig(),//k线图配置
            currency1: '',//币种简称。ETH
            currency2: '',//
            saved_chart: null,
            chart: null,//k线图对象
            feed: null,//数据对象
            last_price: 1234.2365,
            //time：日期(毫秒单位时间戳)，close：收盘价，open：开盘价，high：最高价，low：最低价，volume：交易量
            // bars:[{
            //     time: new Date().getTime(),
            //     close: 10,
            //     open: 8,
            //     high: 11,
            //     low: 7,
            //     volume:50
            // }],
            bars: [],
            page: {
                "area": '',
                "current": 1,
