                loading: false,
                finish: false,
            },
            isShowCharts: false,//是否渲染完图表
        }
    },
    methods: {
        //创建表格数据，自动调用
        createFeed() {
            let _this_ = this;
            let Datafeed = {};
            Datafeed.DataPulseUpdater = function (datafeed, updateFrequency) {
                this._datafeed = datafeed;
                this._subscribers = {};
                this._requestsPending = 0;
                var that = this;
                var update = function () {
                    if (that._requestsPending > 0) {
                        return;
                    }
                    for (var listenerGUID in that._subscribers) {
                        var subscriptionRecord = that._subscribers[listenerGUID];
                        var resolution = subscriptionRecord.resolution;
                        var datesRangeRight = parseInt((new Date().valueOf()) / 1000);
                        var datesRangeLeft = datesRangeRight - that.periodLengthSeconds(resolution, 10);
                        that._requestsPending++;
                        (function (_subscriptionRecord) {
                            that._datafeed.getBars(_subscriptionRecord.symbolInfo, resolution, datesRangeLeft, datesRangeRight, function (bars) {
                                that._requestsPending--;
                                if (!that._subscribers.hasOwnProperty(listenerGUID)) {
                                    return;
                                }
                                if (bars.length === 0) {
                                    return;
                                }
                                var lastBar = bars[bars.length - 1];
                                if (!isNaN(_subscriptionRecord.lastBarTime) && lastBar.time < _subscriptionRecord.lastBarTime) {
                                    return;
                                }
                                var subscribers = _subscriptionRecord.listeners;
                                var isNewBar = !isNaN(_subscriptionRecord.lastBarTime) && lastBar.time > _subscriptionRecord.lastBarTime;
                                if (isNewBar) {
                                    if (bars.length < 2) {
                                        throw new Error('Not enough bars in history for proper pulse update. Need at least 2.');
                                    }
                                    var previousBar = bars[bars.length - 2];
                                    for (var i = 0; i < subscribers.length; ++i) {
                                        subscribers[i](previousBar);
                                    }
                                }
                                _subscriptionRecord.lastBarTime = lastBar.time;
                                for (let i = 0; i < subscribers.length; ++i) {
                                    subscribers[i](lastBar);
                                }
                            },
                                //	on error
                                function () {
                                    that._requestsPending--;
                                });
                        })(subscriptionRecord);
                    }
                };
                if (typeof updateFrequency != 'undefined' && updateFrequency > 0) {
                    setInterval(update, updateFrequency);
                }
            };

            Datafeed.DataPulseUpdater.prototype.periodLengthSeconds = function (resolution, requiredPeriodsCount) {
                var daysCount = 0;
                if (resolution === 'D') {
                    daysCount = requiredPeriodsCount;
                } else if (resolution === 'M') {
                    daysCount = 31 * requiredPeriodsCount;
                } else if (resolution === 'W') {
                    daysCount = 7 * requiredPeriodsCount;
                } else {
                    daysCount = requiredPeriodsCount * resolution / (24 * 60);
                }
                return daysCount * 24 * 60 * 60;
            };

            Datafeed.DataPulseUpdater.prototype.subscribeDataListener = function (symbolInfo, resolution, newDataCallback, listenerGUID) {
                this._datafeed._logMessage('Subscribing ' + listenerGUID);
                if (!this._subscribers.hasOwnProperty(listenerGUID)) {
                    this._subscribers[listenerGUID] = {
                        symbolInfo: symbolInfo,
                        resolution: resolution,
                        lastBarTime: NaN,
                        listeners: []
                    };
                }
                this._subscribers[listenerGUID].listeners.push(newDataCallback);
            };

            Datafeed.DataPulseUpdater.prototype.unsubscribeDataListener = function (listenerGUID) {
                this._datafeed._logMessage('Unsubscribing ' + listenerGUID);
                delete this._subscribers[listenerGUID];
            };

            //container主要数据结构。
            Datafeed.Container = function (updateFrequency) {
                this._configuration = {
                    supports_search: false,
                    supports_group_request: false,
                    // supported_resolutions: ['1','15','60', '1D'],//['1', '3', '5', '15', '30', '60', '120', '240', '360', '720', '1D', '3D', '1W', '1M']
                    // supported_resolutions: ['1D'],//['1', '3', '5', '15', '30', '60', '120', '240', '360', '720', '1D', '3D', '1W', '1M'],
                    supported_resolutions: ['1'],//['1', '3', '5', '15', '30', '60', '120', '240', '360', '720', '1D', '3D', '1W', '1M'],
                    // supportedResolutions:['1M'],
                    supports_marks: true,
                    supports_timescale_marks: true,
                    exchanges: ['smartbit'],//交易所名称
                };
                this._barsPulseUpdater = new Datafeed.DataPulseUpdater(this, updateFrequency || 10 * 1000);
                this._enableLogging = true;
                this._callbacks = {};
                this._initializationFinished = true;
                this._fireEvent('initialized');
                this._fireEvent('configuration_ready');
            };

            Datafeed.Container.prototype._fireEvent = function (event, argument) {
                if (this._callbacks.hasOwnProperty(event)) {
                    var callbacksChain = this._callbacks[event];
                    for (var i = 0; i < callbacksChain.length; ++i) {
                        callbacksChain[i](argument);
                    }
                    this._callbacks[event] = [];
                }
            };

            Datafeed.Container.prototype._logMessage = function (message) {
                if (this._enableLogging) {
                    var now = new Date();
                    // console.log("CHART LOGS: "+now.toLocaleTimeString() + '.' + now.getMilliseconds() + '> ' + message);
                }
            };

            Datafeed.Container.prototype.on = function (event, callback) {
                if (!this._callbacks.hasOwnProperty(event)) {
                    this._callbacks[event] = [];
                }
                this._callbacks[event].push(callback);
                return this;
            };
            //此方法可以设置图表库支持的图表配置
            Datafeed.Container.prototype.onReady = function (callback) {
                let that = this;
                if (this._configuration) {
                    setTimeout(function () {
                        callback(that._configuration);
                    }, 0);
                }
                else {
                    this.on('configuration_ready', function () {
                        callback(that._configuration);
                    });
                }
            };
            //通过商品名称解析商品信息
            //symbolName(商品名称 或ticker), onSymbolResolvedCallback, onResolveErrorCallback
            Datafeed.Container.prototype.resolveSymbol = function (symbolName, onSymbolResolvedCallback, onResolveErrorCallback) {

                this._logMessage("GOWNO :: resolve symbol " + symbolName);
                Promise.resolve().then(() => {
                    function adjustScale() {
                        if (_this_.last_price > 1000)
                            return 100;
                        else
                            return 100000000;
                    }
                    this._logMessage("GOWNO :: onResultReady inject " + 'AAPL');
                    // console.log(symbolName)
                    onSymbolResolvedCallback({
                        // "name": _this_.currency1 + "-"+ _this_.currency2,
                        // "name":"AAPL",
                        "name": _this_.area.text,
                        // "symbol": '55',//商品名称
                        "timezone": "Asia/Shanghai",//"Europe/Warsaw",//这个商品的交易所时区
                        "pricescale": adjustScale(),//价格精度
                        "minmov": 1,//最小波动
                        "minmov2": 0,
                        // "ticker": _this_.currency1 + "-"+ _this_.currency2,
                        "ticker": _this_.area.text,
                        // "ticker": 'smartbit',//它是您的商品体系中此商品的唯一标识符
                        "description": "",//商品说明。这个商品说明将被打印在图表的标题栏中。
                        "session": "24x7",//商品交易时间。7*24小时(例如：比特币或其它数字货币)
                        "type": "bitcoin",//仪表的可选类型。bitcoin比特币 
                        // "exchange-traded": "smartbit",
                        // "exchange-listed": "smartbit",
                        "has_intraday": true,//布尔值显示商品是否具有日内（分钟）历史数据。
                        "intraday_multipliers": ['1'],//这是一个包含日内周期(分钟单位)的数组，datafeed将会自行构建它。
                        "has_weekly_and_monthly": false,//布尔值显示商品是否具有以W和M为单位的历史数据。
                        "has_no_volume": false,//布尔表示商品是否拥有成交量数据。
                        "regular_session": "24x7",
                        // "timeScale.rightOffset": 50,
                        //     'timeScale':{
                        //         'barSpacing':'19.119813271949624'
                        //     }
                    });
                })
            };
            //当图表库需要由日期范围定义的历史K线片段时，将调用此函数
            //旧版getBars回调函数有六个参数，新版的回调函数只有五个。
            //旧版：getBars(symbolInfo, resolution, rangeStartDate, rangeEndDate, onDataCallback, onErrorCallback)
            //新版：getBars(symbolInfo, resolution, periodParams, onHistoryCallback, onErrorCallback)，旧版的rangeStartDate, rangeEndDate这两个值被放在了periodParams，并且新增了两个值。
            Datafeed.Container.prototype.getBars = function (symbolInfo, resolution, rangeStartDate, rangeEndDate, onDataCallback, onErrorCallback) {
                // if (rangeStartDate > 0 && (rangeStartDate + '').length > 10) {
                //     throw new Error(['Got a JS time instead of Unix one.', rangeStartDate, rangeEndDate]);
                // }
                // onDataCallback([], { noData: true });

                /** 标注相关 */
                //清计时器
                // if (_this_.Timer != null) {
                //     clearInterval(_this_.Timer);
                // }
                //我是实时更新标记 所以用了计时器
                // _this_.Timer = setInterval(() => {
                // var marks = _this_.getMarks();
                // let marks = [{
                //     id:'185145',
                //     // time:1675987200000 ,
                //     time:1675987200000 / 1000,
                //     color:'red',
                //     text:'woshibiaoti',
                //     label:'woshineirong',
                //     labelFontColor:'#ffffff',
                //     minSize:20,
                // }]
                // console.log(marks,onDataCallback)
                // onDataCallback(marks);
                // onDataCallback(marks,{ noData: true });
                // }, 1000);
                // 通过$once来监听定时器，在beforeDestroy钩子可以被清除。
                // _this_.$once("hook:beforeDestroy", () => {
                //     clearInterval(_this_.Timer);
                // });
                // setTimeout(()=>{
                // console.log(marks,5566)
                // rangeEndDate(marks);
                // rangeEndDate([], { noData: true });
                // },0);

                // rangeEndDate([]);
                rangeEndDate([], { noData: true });
            };
            Datafeed.Container.prototype.getMarks = function (symbolInfo, startDate, endDate, onDataCallback, resolution) {
                //清计时器
                // if (this_vue.Timer != null) {
                //     clearInterval(this_vue.Timer);
                // }
                // //我是实时更新标记 所以用了计时器
                // this_vue.Timer = setInterval(() => {
                // var marks = this_vue.getMarks();
                //     console.log(marks.length);
                //     onDataCallback(marks);
                // }, 1000);
                // // 通过$once来监听定时器，在beforeDestroy钩子可以被清除。
                // this_vue.$once("hook:beforeDestroy", () => {
                //     clearInterval(this_vue.Timer);
                // });
                // let marks = [{
                //     id:'185145',
                //     // time:1675987200000 ,
                //     time:1675987200000 / 1000,
                //     color:'red',
                //     text:'三只松鼠股东减持2%。',
                //     label:'注',//注，
                //     labelFontColor:'#ffffff',
                //     minSize:20,
                // }]
                var marks = _this_.getMarks();
                setTimeout(() => {
                    // console.log(marks,5566)
                    onDataCallback(marks);
                }, 0);
            };
            //订阅K线数据
            Datafeed.Container.prototype.subscribeBars = function (symbolInfo, resolution, onRealtimeCallback, listenerGUID, onResetCacheNeededCallback) {
                // console.log('onRealtimeCallback',onRealtimeCallback);
                _this_.bars.forEach(function (bar) {
                    // console.log("bar",bar);
                    onRealtimeCallback(bar)
                });
                this.on('pair_change', function () {
                    onResetCacheNeededCallback();
                });
            };
            //取消订阅K线数据
            Datafeed.Container.prototype.unsubscribeBars = function (listenerGUID) {
                this._barsPulseUpdater.unsubscribeDataListener(listenerGUID);
            };
            return new Datafeed.Container;
        },
        //标记
        getMarks() {
            var that = this;
            //自定义背景颜色
            var buyColor = { background: "#4caf50" };
            var sellColor = { background: "#e53935" };
            var marks = [];
            // bsAll是我的标记数据  根据你的情况而定
            // for (var i = 0; i < that.bsAll.length; i++) {
            //     var mark = {};
            //     mark.id = that.bsAll[i].id;  //id 是根据k线的id 确定的唯一性
            //     mark.time = that.bsAll[i].time / 1000; //时间也是唯一的  / 1000取决于后台返回你的数据
            //     mark.color = that.bsAll[i].bs === "买" ? buyColor : sellColor;
            //     mark.text = that.bsAll[i].bs;
            //     mark.label = that.bsAll[i].bs; 
            //     mark.labelFontColor = "#fff";//文字颜色
            //     mark.minSize = 20;//尺寸
            //     marks.push(mark);
            // }
            //标记数据
            let bsAll = [{
                id: 232323,
                time: '2023-02-10 17:00:00',
                type: 'buy',
                text: '港股节期停市！',
                label: '注',
            }, {
                id: 23233,
                time: '2023-02-05 17:00:00',
                type: 'sell',
                text: '三只松鼠股东减持2%！',
                label: '事',
            }]
            bsAll = []
            bsAll.map((v, o) => {
                var mark = {};
                mark.id = v.id;  //id 是根据k线的id 确定的唯一性
                // mark.time = v.time / 1000; //时间也是唯一的  / 1000取决于后台返回你的数据
                mark.time = new Date(v.time).getTime() / 1000;
                mark.color = v.type == "buy" ? buyColor : sellColor;
                mark.text = v.text;
                mark.label = v.label;
                mark.labelFontColor = "#fff";//文字颜色
                mark.minSize = 20;//尺寸
                marks.push(mark);
            });
            return marks;
        },
        //初始化图表
        async init() {
            this.feed = this.createFeed();
            this.tradingViewConfig.symbol = this.currency1 + ":" + this.currency2;//'AAPL'
            this.tradingViewConfig.datafeed = this.feed;
            // this.tradingViewConfig.datafeed = new Datafeeds.UDFCompatibleDatafeed("https://demo-feed-data.tradingview.com");//this.feed;
            this.chart = window.tvWidget = new TradingView.widget(this.tradingViewConfig);
            this.chart.onChartReady(() => {
                this.updateKLine();
                this.isShow = true;
                //切换分时图
                // this.chart.subscribe("time_interval",res=>{
                //     localStorage.setItem('tradingview_interval',res.label);
                //     this.bars=[];
                //     this.changePair();
                //     sendMsg({cmd:'tradingview',data:res.label});
                // });
                //重置按钮 undo_redo_state_changed：缩放改变事件
                this.chart.subscribe("reset_scales", () => {
                    this.updateKLine();
                })
                const onLoad = ({ from, to }) => {
                    // console.log('onVisibleRangeChanged:',from, to,getThisTime(from).now)
                    // console.log('onVisibleRangeChanged:',from, to,this.bars[0].time/1000,this.page.finish,this.page.loading);
                    let t = from - this.bars[0].time / 1000;
                    // if(this.bars.length>0 && from==this.bars[0].time/1000){
                    if (this.isShowCharts && !this.page.loading && !this.page.finish && this.bars.length > 0 && t < 24 * 60 * 60 && t > 0) {//tradingview转成了整点时间，需要对比小于24小时以内。
                        console.log('第一条k线：', from);
                        this.page.current += 1;
                        this.getList(() => {
                            console.log('更新图表')
                            this.changePair();
                        });
                    }
                }
                //选中k线，点击k线 
                //time：当前列的时间，price：当前列鼠标高度的价格
                //需要判断时间是否存在数据中，再获取相关的数据（开盘价，收盘价等等）
                this.chart.activeChart().crossHairMoved().subscribe(null, ({ time, price }) => {
                    // console.log('选中k线：',time, price)
                    return;
                    this.bars.map((v, o) => {
                        if (v.time == time * 1000) {
                            console.log('相同：', v.time, time)
                            this.$emit('select_line', { time: v.time });//选中K线
                        }
                    })
                });
                //释放
                this.chart.subscribe("mouse_up", () => {
                    this.$emit('up_line', {});//释放选中
                })
                //订阅可见逻辑范围更改
                //判断是否需要获取历史数据
                this.chart.activeChart().onVisibleRangeChanged().subscribe(null, ({ from, to }) => {
                    // console.log('选中：',from,to)
                    // throttle(onLoad({ from, to }),50)
                    // // console.log('onVisibleRangeChanged:',from, to,getThisTime(from).now)
                    // // console.log('onVisibleRangeChanged:',from, to,this.bars[0].time/1000,this.page.finish,this.page.loading);
                    // let t=from-this.bars[0].time/1000;
                    // // if(this.bars.length>0 && from==this.bars[0].time/1000){
                    // if(!this.page.loading && !this.page.finish && this.bars.length>0 &&  t< 24*60*60 && t>0){//tradingview转成了整点时间，需要对比小于24小时以内。
                    //     console.log('第一条k线：',from);
                    //     this.page.current+=1;
                    //     // this.getList(()=>{
                    //     //     console.log('更新图表')
                    //     //     this.changePair();
                    //     // });
                    // }
                });
                //函数节流方法
                function throttle(fn, dalay, atleast = 0) {
                    let timer = null
                    let previous = null
                    return (...args) => {
                        let now = +new Date() //获取当前时间戳
                        !previous ? now : previous
                        if (atleast && now - previous > atleast) {
                            fn.apply(this, args)
                            // 重置上一次开始时间为本次结束时间
                            previous = now
                            clearTimeout(timer)
                        } else {
                            clearTimeout(timer)
                            timer = setTimeout(() => {
                                fn.apply(this, args)
                                previous = null
                            }, dalay)
                        }
                    }
                }
                //均线
                // this.chart.chart().createStudy("Moving Average", false, false, [5, "close", 0], null, { "Plot.color": "#e7eff2", "Plot.linewidth": 0, });
                // this.chart.chart().createStudy("Moving Average", false, false, [10, "close", 0], null, { "Plot.color": "#e6f562", "Plot.linewidth": 0, });
                // this.chart.chart().createStudy("Moving Average", false, false, [20, "close", 0], null, { "Plot.color": "#cc01bf", "Plot.linewidth": 0, }); 
            });
        },
        //创建随机数据
        createData() {

            //随机k线图
            setInterval(() => {

                let data = this.bars;
                //time：日期(毫秒单位时间戳)，close：收盘价，open：开盘价，high：最高价，low：最低价，volume：交易量
                let lastPrice = data[data.length - 1].low;
                let p = lastPrice * 0.1;
                data.push({
                    time: new Date().getTime(),
                    close: createAutoData(1, lastPrice + p, lastPrice - p)[0],
                    open: createAutoData(1, lastPrice + (p / 2), lastPrice - (p / 2))[0],
                    high: createAutoData(1, lastPrice + p, lastPrice - p)[0],
                    low: createAutoData(1, lastPrice + p, lastPrice - p)[0],
                    volume: 100000 * Math.random()
                });
                this.bars = data;
                this.changePair();
            }, 5000)
        },
        //根据接口获取
        async getList2(callback) {
            let arr = [];
            this.list.forEach(item => {
                arr.push({
                    time: item.time,
                    close: item.close,
                    open: item.open,
                    high: item.height,
                    low: item.low,
                    volume: item.totalVol,
                })
            })
            arr = arr.reverse()
            // this.bars=arr;
            console.log('拿到数据了:', arr, this.list)
            //定时渲染数据
            let len = arr.length;
            let i = 0;
            timeId = setInterval(() => {
                this.bars.push(arr[i]);
                this.changePair();
                i++;
                if (i >= len) {
                    clearInterval(timeId);
                }
            }, 1000);

            //分页判断
            // this.isShowCharts=false;
            // setTimeout(()=>{
            //     this.isShowCharts=true;
            // },500);

            if (callback) {
                callback();
            }
        },
        async getList(callback) {
            console.log(callback, 'callback,timeView_tradingview')
            if (this.page.loading) {
                return;
            }
            let d = {
                "area": this.area.type,
                "current": this.page.current,
                "size": this.page.size,
            };
            this.page.loading = true;
            // let res = await this.$api.trading_view_page(d);
            // let list = res.list || [];
            let list = this.list;
            let arr = [];
            list.forEach(item => {
                // arr.push({
                //     time: item.time,
                //     close: item.close,
                //     open: item.open,
                //     high: item.highest,
                //     low: item.lowest,
                //     volume: item.totalAmount,
                // })
                arr.push({
                    time: item.time,
                    close: item.close,
                    open: item.open,
                    high: item.height,
                    low: item.low,
                    volume: item.totalVol,
                })
            })
            arr = arr.reverse()
            this.bars = arr;
            console.log('拿到数据了:', arr, this.list)

            this.isShowCharts = false;
            setTimeout(() => {
                this.isShowCharts = true;
            }, 500);

            if (callback) {
                callback();
            }
            // this.changePair();
        },
        //连接socket
        initSocket() {
            // this.initData();
            // this.createData();
            if (!this.chart) {
                this.init();
            } else {
                this.changePair();
            }
            this.getList(() => {
                this.changePair();
            });



            // this.$eventBus.$on("tradingview_msg",(res)=>{
            //     // console.log("tradingview:" ,res);
            //     let type = res.data.type;
            //     this.currency1 = this.$store.state.blockchain.abbreviation;
            //     if(type=='get'){    //首次进入获取数据
            //         this.bars=res.data.data || [];
            //         if(!this.chart){
            //             this.init();
            //         }else{
            //             this.changePair();
            //         }
            //     }else if(type=='add'){      //新增数据
            //         this.bars.push(res.data.data);
            //         this.changePair();
            //     }
            // });
            // sendMsg({cmd:'tradingview',data: localStorage.getItem('tradingview_interval') || '1D'});
        },
        //更新图表
        changePair() {
            if (this.chart && this.feed) {
                this.feed._fireEvent('pair_change');
                this.chart.activeChart().zoomOut();
                // this.chart.activeChart().createExecutionShape().setArrowSpacing(50);
                // this.chart.setSymbol('最帅','1');
                //切换名称和周期
                // this.currency1 = this.$store.state.blockchain.abbreviation;
                // this.chart.activeChart().setSymbol(this.currency1+"-"+this.currency2, () => {
                this.chart.activeChart().setSymbol(this.area.text, () => {
                });
                this.chart.activeChart().resetData();
            }
        },
        //重新绘制k线大小
        updateKLine() {
            return; //以下代码造成一直加载中
            let len = this.bars.length;
            if (len > 0) {
                this.chart.chart().setVisibleRange({ from: this.bars[len > 60 ? len - 60 : 0].time / 1000, to: this.bars[len - 1].time / 1000 });
            }
        },
        //获取数据
        getData() {
            this.page.current = 1;
            this.page.finish = false;
            setTimeout(() => {
                this.initSocket();
            }, 0)
        }
    },
    mounted() {
        // setTimeout(()=>{
        //     this.initSocket();
        // },0)
    },
    computed: {
        //多语言切换
        lang() {
            return this.$i18n.locale;
        }
    },
    watch: {
        //监听多语言切换
        lang() {
            this.tradingViewConfig.locale = `${this.$t("locale")}`;
            this.tradingViewConfig.datafeed = this.feed;
            this.chart = window.tvWidget = new TradingView.widget(this.tradingViewConfig);
        },
        pair(newVal, oldVal) {
            this.currency1 = newVal[0];
            this.currency2 = newVal[1];
            this.changePair();
            console.log('pair')
        },
    },
    beforeDestroy() {
        this.$eventBus.$off("tradingview_msg");
        this.chart = null;
        clearInterval(timeId);
    }
}
