<template>
    <div class="flexColumn" style="flex:1;overflow: auto;">
        <!-- <div class="subMenus borderBottom borderLeft borderRight">
            <div v-for="(v,o) in areaList" :key="o" class="subMenu" :class="{'active':o==areaActive}" @click="changeArea(o)">{{ v.text }}</div>
        </div> -->
        <div class="flexBox tradingBox borderLeft borderRight " style="flex:1;overflow: auto;">
            <typeList :area="area" @select="select" ref="typeList"></typeList>
            <div class="flexBox " style="flex:1;flex-direction:column;">
                <centerTop v-model="menuActive" :area="area"></centerTop>
                <div style="flex:1;overflow: auto;">
                    <tradingview v-show="menuActive==0" :area="area" class="relative tradingview wh100" ref="tradingview"></tradingview>
                    <timeView v-show="menuActive==1" :area="area" class="relative wh100"></timeView><!--分时图-->
                    <timeTable v-if="menuActive==2" :area="area" class="relative wh100"></timeTable><!--分价表-->
                    <depthChart v-else-if="menuActive==3" class="relative wh100"></depthChart><!--深度图-->
                    <chipView v-else-if="menuActive==4" class="relative wh100"></chipView><!--筹码分布-->
                    <div v-else-if="menuActive==5" class="relative wh100"></div><!--多周期跳页面-->
                </div>
                <centerBottom :isOpen="isOpen" @toggle="toggle" :area="area" :style="{'height':(isOpen?50:30)+'%'}"></centerBottom>
            </div>
            <handicap :area="area" ref="handicap"></handicap>
        </div>
    </div>
</template>

<script>
import typeList from './typeList'
import tradingview from './tradingview'
import timeView from './timeView'
import timeTable from './timeTable'
import depthChart from './depthChart'
import chipView from './chipView/chipView'
import centerTop from './centerTop'
import centerBottom from './centerBottom/centerBottom'
import handicap from './handicap'
export default {
    name: '',
    props: {
    },
    components: {
        typeList, tradingview, centerTop, centerBottom, handicap,timeView,timeTable,depthChart,chipView
    },
    data() {
        return {
            menuActive:0,//菜单。分时图...
            areaActive:1,//区域。
            areaList:this.$store.state.app.areaList,//区域列表
            area:{},
            isOpen:JSON.parse(sessionStorage.getItem('tradeMainRight_isOpen')) || false,//是否展开
        }
    },
    created(){
        // this.area=this.areaList[this.areaActive];
    },
    mounted() {
        // this.$nextTick(()=>{
        //     this.changeArea(1);
        // })
        const { area } = this.$route.query;
        if(area){
            var index= this.areaList.findIndex((v) => {
                return v.type ==  area;
            });
            this.changeArea(index || 1);//不带参数，默认选择。
        }else{
            this.changeArea(1);
            // this.$nextTick(()=>{
            //     this.$refs['typeList'].trigger();
            // });
        }
    },
    methods: {
        //左边列表切换菜单
        select(v){
            this.area=v;
            this.$router.replace(`/trading?area=${v.type}`);
            // this.$nextTick(()=>{
            //     this.$refs['tradingview'].getData();
            //     this.$refs['handicap'].getInfo();
            // })
        },
        //顶部切换菜单
        changeArea(o){
            this.areaActive=o;
            this.area=this.areaList[this.areaActive];
            // this.$nextTick(()=>{
            //     this.$refs['tradingview'].getData();
            //     this.$refs['handicap'].getInfo();
            // })
        },
        //切换展开折叠
        toggle(val){
            this.isOpen=val!='open'?true:false;
            sessionStorage.setItem('tradeMainRight_isOpen',JSON.stringify(this.isOpen));
            location.reload();
        },
    },
    computed:{
        // area(){
        //     return this.areaList[this.areaActive];
        // }
    }
}
</script>

<style scoped lang='less'>
.tradingBox {
    // height:calc(100% - 48px - 42px);
}

.tradingview {}
</style>