<template>
    <div class="relative borderTop wh100" style="overflow: auto;">
        <div  class="absolute wh100" @scroll="scrollEvent" style="overflow: auto;">
            <div v-for="(v, o) in list" :key="o" @click="toDetail(v)" class="list ">
                <div class="alignCenter">
                    <span class="alignCenter" style="flex:1;">
                        <!-- <span class="ml10 op7">[&nbsp;<span class="">{{v.content}}</span>&nbsp;]</span> -->
                        <span class="title text_deal" :title="v.title" style="flex:1;">{{v.title || '无标题'}}</span>
                    </span>
                    <!-- <span class="op5">{{v.time | getDateDiff}}</span> -->
                </div>
                <div class="f12 op7 mt4 alignCenter">
                    <span class="text_deal" style="flex:1;">{{ v.nickname }}</span>
                    <span>{{ v.time | getDateDiff}}</span>
                </div>
            </div>
            <noData v-if="!page.loading" :list="list" style="flex-shrink: 0; width: 100%;"></noData>
            <!-- <noData :list="list" style="flex-shrink: 0; width: 100%;"></noData> -->
        </div>
        <div v-if="isSendPop" class="sendPop">
            <div style="">
                <Input v-model="form.title" placeholder="标题" type="text" maxlength="50" class="" style="width:60%;"></Input>
            </div>
            <div class="mt10" style="">
                <Input v-model="form.content" placeholder="说点什么呢..." type="textarea" maxlength="5000" :rows="3" class="" style="width:60%;height:100%;"></Input>
            </div>
            <div class="mt10 pb10">
                <Button type="primary" style="min-width:100px;" @click="submit">提交</Button>
                <Button style="margin-left: 15px;min-width:100px;" @click="isSendPop=false">关闭</Button>
            </div>
        </div>
        <div v-if="!isSendPop" class="sendBtn">
            <!-- 发文 -->
            <Button type="primary" style="min-width:80px;" @click="form.parentId='';isSendPop=true;">发表观点</Button>
        </div>
    </div>
</template>
    
<script>
export default {
    name: '',
    props: {
        area:{
            type:Object,
            default:()=>{}
        }
    },
    components: {

    },
    data() {
        return {
            active: 0,
            list:[],
            page:{
                // "type": '热点新闻',
                // area:'china',
                "current": 1,
                "size": 20,
                loading:false,
                finish:false,
            },
            form:{
                content:'',
                parentId:'',
            },
            isSendPop:false,
        }
    },
    mounted() {
        this.getList();
    },
    methods: {
        //重新初始化分页
        init(){
            this.page={
                "current": 1,
                "size": 20,
                loading:false,
                finish:false,
            }
            this.list=[];
            this.getList();
        },
        toDetail(v) {
            localStorage.setItem('comment_detail', JSON.stringify(v));
            this.$router.push(`/commentDetail`);
            // window.open(`/commentDetail`,'_blank');
        },
        //发送
        submit(){
            if(!this.form.title){
                this.$Message.error('请输入标题!');
                return;
            }
            if(!this.form.content){
                this.$Message.error('请输入内容!');
                return;
            }
            let d={
                area: this.area.type,
                title:this.form.title,
                "content": this.form.content,
                // "parentId": this.form.parentId,
            }
            this.$api.point_viewpoint_push(d).then(res=>{
                this.$Message.success('发送成功!');
                this.form.title=''
                this.form.content=''
                this.isSendPop=false;
                this.init();
            });
        },
        // 滚定监听
        scrollEvent(e) {
            let that=this;
            if (Math.ceil(e.currentTarget.scrollTop + e.currentTarget.clientHeight) >=e.currentTarget.scrollHeight-20) {   //容差：20px
                console.log('滚动到底部');
                if(!that.page.finish && !that.page.loading){
                    that.page.current+=1;
                    that.getList();
                }
            }
        },
        async getList() {
            if (this.page.loading) {
                return;
            }
            let d = {
                // "name": this.page.name,
                // "type": this.page.type,
                "area": this.area.type,
                "current": this.page.current,
                "size": this.page.size,
            };
            this.page.loading = true;
            let res = await this.$api.point_viewpoint_page(d);
            // let res = await this.$api.news_page(d);
            let list = res.list || [];
            if (list.length >= d.size) {
                this.page.finish = false;
            } else {
                this.page.finish = true;
            }
            if (d.current == 1) {
                this.list = list;
            } else {
                this.list = list.concat(this.list);
            }
            this.page.loading = false;
        },
    },
    watch:{
        'area':{
            handler:function(val,value){
                console.log('修改')
                this.init();
            },
            deep: true,
            immediate: false
        }
    }
}
</script>
    
<style scoped lang='less'>
.list {
    cursor: pointer;
    padding: 8px 12px;
    position: relative;
}
.list::after{
    content:'';
    position: absolute;
    bottom:0;
    left:50%;
    transform: translate(-50%,0);
    width:calc(100% - 50px);
    height:1px;
    background: #292828;
}

.list:hover {
    color: #fff;

    background: #000;

    // .title {
    //     opacity: 10;
    //     color: @yellow;
    // }

    // .op5{opacity: 10;}
}
.sendPop{
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction:column;
    padding: 10px;
    box-sizing: border-box;
    background: #17171a;
}
.sendBtn{
    position: absolute;
    bottom: 10px;
    right: 10px;
}
</style>